#App {
  background-size: cover;
  background-repeat: no-repeat;
}
#main-container {
  font-family: "Arvo";
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  height: 100vh;
  width: 100vw;
  padding-top: 5vh;
}
#friend-page-profile-pic {
  width: 12vw;
  height: 12vw;
  border-radius: 100%;
  margin-left: 43vw;
}
#friend-profile-pic {
  display: none;
}
#friendly {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
#removeFriend {
  width: 6vw;
  margin: auto;
  margin-top: 1vw;
}
#removeFriend:hover {
  cursor: pointer;
}



